body {
    margin: 0;
    padding: 0;
  }
  
  .video-container {
      position: relative;
      width: 100%;
      height: 0;
      padding-top: 54.55%; /* 16:9 비율의 동영상 화면 비율 */
      overflow: hidden;
    }
    
    .responsive-video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  